@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+Display:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sarabun:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Sarabun", sans-serif;
}

h1, h2, h3, h4, h5 {
  color: rgb(80, 80, 80);
}

.container {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}

blockquote {
  font-style: oblique;
  position: relative;
  background-color: rgba(236, 236, 236, 0.3019607843);
  padding: 40px;
  margin: 20px 0;
  border-radius: 5px;
  font-size: 20px;
}
blockquote p {
  display: inline;
}
blockquote::before {
  position: absolute;
  content: open-quote;
  font-size: 50px;
  margin-left: -0.6em;
  margin-top: -0.4em;
  color: rgba(0, 0, 0, 0.507);
}
blockquote:after {
  position: absolute;
  content: close-quote;
  font-size: 50px;
  bottom: 0;
  right: 0;
  margin-right: 20px;
  margin-bottom: 0;
  color: rgba(0, 0, 0, 0.514);
}

@media (max-width: 1199.98px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 991.98px) {
  .container {
    padding: 0 25px;
  }
}
.edrea-main h1 {
  font-size: 38px;
  font-weight: 600;
}
.edrea-main h2 {
  font-size: 28px;
  font-weight: 600;
}
.edrea-main h3 {
  font-size: 23px;
  font-weight: 600;
}
.edrea-main h4 {
  font-size: 18px;
  font-weight: 600;
}
.edrea-main ul, .edrea-main ol {
  margin: 0;
  padding: 0;
  padding-left: 20px;
}
.edrea-main a {
  text-decoration: none;
  color: #005792;
  font-weight: 600;
  transition: 0.3s;
}
.edrea-main a:hover {
  color: #e77221;
}
.edrea-main p {
  font-size: 17px;
  line-height: 1.6;
  color: rgb(80, 80, 80);
}

.edrea-archive-blog {
  margin: 60px 0;
}
.edrea-archive-blog .swiper {
  margin-bottom: 60px;
}
.edrea-archive-blog .swiper .swiper-button-next, .edrea-archive-blog .swiper .swiper-button-prev {
  top: unset;
  border: 1px solid #005792;
  padding: 5px 15px;
  color: #e77221;
  border-radius: 3px;
  bottom: 0;
  width: 25px;
  height: 25px;
  transition: 0.3s;
}
.edrea-archive-blog .swiper .swiper-button-next::after, .edrea-archive-blog .swiper .swiper-button-prev::after {
  font-size: 14px;
}
.edrea-archive-blog .swiper .swiper-button-next:hover, .edrea-archive-blog .swiper .swiper-button-prev:hover {
  background-color: #005792;
  color: #FFF;
}
.edrea-archive-blog .swiper .swiper-pagination {
  display: none;
}
.edrea-archive-blog .swiper .swiper-button-prev {
  left: unset;
  right: 100px;
}
.edrea-archive-blog .swiper .swiper-button-next {
  right: 40px;
}
.edrea-archive-blog__slider--item {
  display: flex !important;
  align-items: flex-start;
  justify-content: space-between;
}
.edrea-archive-blog__slider--item .trending-thumbnail {
  width: calc(50% - 30px);
}
.edrea-archive-blog__slider--item .trending-thumbnail img {
  width: 100%;
  object-fit: cover;
  height: 480px;
  border-radius: 10px;
}
.edrea-archive-blog__slider--item .trending-detail {
  width: calc(50% - 30px);
}
.edrea-archive-blog__slider--item .trending-detail .trending-title {
  font-size: 40px;
  color: rgb(80, 80, 80);
}
.edrea-archive-blog__slider--item .trending-detail .trending-excerpt {
  font-size: 25px;
  margin-bottom: 60px;
  line-height: 1.4;
}
.edrea-archive-blog__slider--item .trending-detail .trending-button {
  padding: 10px 20px;
  color: #FFF;
  background-color: #005792;
  text-decoration: none;
  font-size: 20px;
  border-radius: 5px;
  transition: 0.3s;
}
.edrea-archive-blog__slider--item .trending-detail .trending-button:hover {
  background-color: #53CDE2;
}
.edrea-archive-blog__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 30px 30px;
  grid-template-areas: ". . ." ". . ." ". . .";
  justify-content: space-evenly;
  justify-items: center;
  align-content: space-evenly;
  align-items: center;
}
.edrea-archive-blog .edrea-load-more {
  display: flex;
  justify-content: center;
}
.edrea-archive-blog .edrea-load-more button {
  background-color: #005792;
  color: #FFF;
  width: 160px;
  padding: 10px;
  font-size: 17px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  z-index: 777;
}

@media (max-width: 1199.98px) {
  .edrea-archive-blog__slider--item .trending-detail .trending-title {
    font-size: 32px;
  }
  .edrea-archive-blog__slider--item .trending-detail .trending-excerpt {
    font-size: 18px;
  }
  .edrea-archive-blog__slider--item .trending-detail .trending-button {
    font-size: 16px;
  }
}
@media (max-width: 991.98px) {
  .edrea-archive-blog__slider--item {
    flex-direction: column;
    padding: 20px 0;
    text-align: center;
  }
  .edrea-archive-blog__slider--item .trending-thumbnail {
    width: 100%;
  }
  .edrea-archive-blog__slider--item .trending-detail {
    width: 100%;
  }
  .edrea-archive-blog .swiper .swiper-pagination {
    display: block;
    top: 470px;
  }
  .edrea-archive-blog .swiper .swiper-button-next, .edrea-archive-blog .swiper .swiper-button-prev {
    display: none;
  }
}
.edrea-single-post {
  padding: 30px 0;
}
.edrea-single-post__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.edrea-single-post__wrapper--left {
  width: calc(72% - 25px);
}
.edrea-single-post__wrapper--left p, .edrea-single-post__wrapper--left li {
  font-size: 17px;
  line-height: 1.6;
  color: rgba(0, 0, 0, 0.8509803922);
}
.edrea-single-post__wrapper--right {
  width: calc(28% - 25px);
  padding: 20px 0;
}
.edrea-single-post__title {
  margin-bottom: 0;
}
.edrea-single-post__meta {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  margin-top: -10px;
}
.edrea-single-post__meta--author {
  display: flex;
  align-items: center;
  margin-right: 25px;
}
.edrea-single-post__meta--author img {
  margin-right: 8px;
  border-radius: 50%;
}
.edrea-single-post__meta--author .edrea-author-name {
  text-transform: uppercase;
  font-weight: 600;
  color: #005792;
  font-size: 15px;
}
.edrea-single-post__meta .post-date {
  display: flex;
}
.edrea-single-post__meta .post-date img {
  width: 25px;
  margin-right: 8px;
}
.edrea-single-post__meta .post-date p {
  color: rgba(0, 0, 0, 0.8509803922);
  font-weight: 600;
  text-transform: uppercase;
  font-size: 15px;
}

.edrea-header {
  padding: 30px 0;
  background-color: #005792;
}
.edrea-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.edrea-header__wrapper .site-branding {
  width: calc(27% - 20px);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.edrea-header__wrapper .site-branding .site-description {
  margin: 0;
  color: #FFF;
  font-style: italic;
  margin-left: 20px;
  font-weight: 600;
}
.edrea-header__wrapper .main-navigation {
  width: calc(46% - 20px);
  display: flex;
  align-items: center;
}
.edrea-header__wrapper .main-navigation ul {
  list-style: none;
